
.twoToOneAnimation {
  -webkit-animation: slideRightFromMid 166.667ms linear 1;
          animation: slideRightFromMid 166.667ms linear 1;
}

@-webkit-keyframes slideRightFromMid {
  from {
    left: 27%;
  }
  to {
    left: 54%;
  }
}

@keyframes slideRightFromMid {
  from {
    left: 27%;
  }
  to {
    left: 54%;
  }
}

.oneToZeroAnimation {
  -webkit-animation: slideLeftFromRight 333.333ms linear 1;
          animation: slideLeftFromRight 333.333ms linear 1;
}

@-webkit-keyframes slideLeftFromRight {
  from {
    left: 54%;
  }
  to {
    left: 7%;
  }
}

@keyframes slideLeftFromRight {
  from {
    left: 54%;
  }
  to {
    left: 7%;
  }
}

.zeroToTwoAnimation {
  -webkit-animation: slideMidFromLeft 166.667ms linear 1;
          animation: slideMidFromLeft 166.667ms linear 1;
}

@-webkit-keyframes slideMidFromLeft {
  from {
    left: 7%;
  }
  to {
    left: 27%;
  }
}

@keyframes slideMidFromLeft {
  from {
    left: 7%;
  }
  to {
    left: 27%;
  }
}



